import { Component, OnInit } from '@angular/core';
import { PaginatorModule } from 'primeng/primeng';
import { WorkflowHttpService } from '../../../workflow/http/workflow.http.service';
import { Message } from 'primeng/primeng';
import { ConfirmationService } from 'primeng/primeng';
import { AlreadyWorkHistoryBean } from './bean/already-work-history.bean';
import { TreeNode } from 'primeng/primeng';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { CALENDAR_CN, tableMessage } from 'app/@uisftech/common/constant/config';

@Component({
  selector: 'already-work',
  templateUrl: './already-work.component.html',
  styleUrls: ['./already-work.component.scss'],
	providers: [Commfunc]
})
export class AlreadyWorkComponent implements OnInit {
  //
  headerTitle: string = '';
  //
  showModel: any;
  //
  display: boolean = false;
  //
  historyValue: any;
  //
  msgs: Message[] = [];
  // 
  tableData: any;
  //
  total: number;
  total1:string;//
  tabMesg: any = tableMessage;//
  ch: any = CALENDAR_CN;        //
  temData: Date = new Date();   //

  showTree: Boolean = false;
  //value
  choiceValue: any;
  //  
  selectedFile: TreeNode;
  //
  fileTree: any;
  left_tree: any;
  endAfter: any;//  

  alreadyWorkHistoryBean: AlreadyWorkHistoryBean = new AlreadyWorkHistoryBean();
  constructor(
    public workflowHttpService: WorkflowHttpService,
    public commfunc: Commfunc
  ) { }

  ngOnInit() {
    this.alreadyWorkHistoryBean.rows = 10;
    this.alreadyWorkHistoryBean.page = 1;
    this.queryClick();
  }


  //
  queryClick() {
    /**
     * userId
     */
    this.total1='';
    if (this.selectedFile) {
      this.alreadyWorkHistoryBean.processDefinitionId = this.choiceValue;
    }
    this.alreadyWorkHistoryBean.endAfter = this.commfunc.transDateN(this.endAfter);
    let ch = JSON.parse(this.commfunc.getSessionData('commonHeader'));
    this.alreadyWorkHistoryBean.userId = ch['userId'];

    this.workflowHttpService.IWorkFlowServicegetNoEndTaskList(this.alreadyWorkHistoryBean).subscribe(data => {
      if (data.msg.returnCode.code == 'success') {
        this.tableData = data.msg.data
        this.total = data.msg.total;
        if(this.total==0){
          this.total1='0'
        }
        this.msgs = [];
        this.msgs.push({ severity: 'success', summary: '', detail: '' });
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '', detail: data.msg.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '', detail: '' });
    })
  }
  //
  over() {
    let ch = JSON.parse(this.commfunc.getSessionData('commonHeader'));
    let param = {
      userId: 'sys',
      taskId: this.tableData[0].taskId,
      nodeName: this.tableData[0].taskName
    }
    this.workflowHttpService.IWorkFlowServiceback(param).subscribe(data => {
      if (data.msg.returnCode.code == 'success') {
        this.msgs = [];
        this.msgs.push({ severity: 'success', summary: '', detail: '' });
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '', detail: data.msg.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '', detail: '' });
    })
  }
  //
  reset() {
    this.selectedFile = null;
    this.endAfter = '';
    this.alreadyWorkHistoryBean.processDefinitionId = '';
  }
  //
  history(car) {
    this.headerTitle = '';
    this.display = true;
    this.showModel = '1';
    this.historyValue = car;
  }
  //
  historyCall() {
    this.display = false;
    this.queryClick();
  }
  //
  permissionCheck(id){
	  return	this.commfunc.permissionCheck(id)
	}

  //
  paginate(event) {
    //
    this.alreadyWorkHistoryBean.rows = event.rows * 1;
    //
    let currentPage = event.page + 1;
    this.alreadyWorkHistoryBean.page = currentPage;
    //
    this.queryClick();
    this.alreadyWorkHistoryBean.page = 1;
  }
}
