import { Component, OnInit } from '@angular/core';
import { PaginatorModule } from 'primeng/primeng';
import { WorkflowHttpService } from '../../../workflow/http/workflow.http.service';
import { Message } from 'primeng/primeng';
import { ConfirmationService } from 'primeng/primeng';
import { HistoryFlowBean } from './bean/history.bean';
import { TreeNode } from 'primeng/primeng';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
// import { PartyType } from './../../constant/codeValue';
import { CALENDAR_CN, tableMessage } from 'app/@uisftech/common/constant/config';
@Component({
  selector: 'history-flow',
  templateUrl: './history-flow.component.html',
  styleUrls: ['./history-flow.component.scss'],
  providers: [Commfunc]
})
export class HistoryFlowComponent implements OnInit {
  // 
  tabMesg: any = tableMessage;
  aaa: any;
  // 
  precredit: string = '';
  //
  updateValue: any = [];
  // 
  tableData: any;
  //
  total: string;
  //
  headerTitle: string = '';
  showModel: any;
  //
  display: boolean = false;
  //
  inValue: string;
  //
  msgs: Message[] = [];
  p_data: any;
  states: any[] = [];//
  state: string = 'completed';
  historyFlowBean: HistoryFlowBean = new HistoryFlowBean();
  historyValue: any;//
  ch: any = CALENDAR_CN;        //
  temData: Date = new Date();   //

  showTree: Boolean = false;
  //value
  choiceValue: any;
  //  
  selectedFile: TreeNode;
  //
  fileTree: any;
  left_tree: any;
  startTime: any;//  
  endAfter: any;//
  changeValue: string;//
  code: any;
  // 
  expandRecursive(node: TreeNode, isExpand: boolean) {
    node.expanded = isExpand;
    if (node.children) {
      node.children.forEach(childNode => {
        this.expandRecursive(childNode, isExpand);
      });
    }
  }
  constructor(
    public workflowHttpService: WorkflowHttpService,
    public commfunc: Commfunc,
  ) {

    this.codeValues() //
    this.states = this.code['PartyType'];
  }
  ngOnInit() {
    this.historyFlowBean.rows = 10;
    this.historyFlowBean.page = 1;
    this.queryClick();
    this.queryClick1();
    //
    let _thiss = this;
    document.onkeydown = function (event) {
      if (event && event.keyCode === 13) {
        _thiss.queryClick();
      }
    }
  }
  //
  codeValues() {
    this.commfunc.codeValue();
    let ret = this.commfunc.codeDatas;
    return this.code = ret //code
  }
  //
  toTree(data) {
    data.forEach(item => {
      item['label'] = item['text']
      item['value'] = item
      if (item.children) {
        this.toTree(item.children)
      }
    });
    return data
  }
  //
  nodeSelect(event) {
    if (event.node) {
      this.choiceValue = event.node.value.id;
      this.selectedFile = event.node.label;
      this.showTree = false
    }
  }

  nodeUnselect(event) {
    this.msgs = [];
    this.msgs.push({ severity: 'info', summary: 'Node Unselected', detail: event.node.label });
  }
  //
  queryClick1() {
    this.workflowHttpService.IProcessDirectoryServiceSearchForDesigner({ 'uniqueReqData': '-1' }).subscribe(data => {
      if (data.msg.returnCode.code == 'success') {
        this.left_tree = this.toTree(data.msg.listArray);
        this.left_tree.forEach(node => {
          this.expandRecursive(node, true);
        });

      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '', detail: data.errorMessage });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '', detail: '' });
    })
  }
  //
  showData() {
    this.temData = this.endAfter;
  }
  isChange() {
    this.changeValue = '1'
  }
  //
  queryClick() {
    if (this.selectedFile) {
      this.historyFlowBean.processDefinitionId = this.choiceValue;
    }
    this.historyFlowBean.endAfter = this.commfunc.transDateN(this.endAfter);
    let ch = JSON.parse(this.commfunc.getSessionData('commonHeader'));
    this.historyFlowBean.userId = ch['userId'];
    if (this.changeValue) {
      this.historyFlowBean.state = this.state;
    } else {
      this.historyFlowBean.state = 'completed'
    }
    this.workflowHttpService.IWorkFlowServicegetProcessInsList(this.historyFlowBean).subscribe(data => {
      if (data.msg.returnCode.code == 'success') {
        this.tableData = data.msg.data
        this.total = data.msg.total;
        if (this.total == '0') {
          this.total = '0'
        }
        this.msgs = [];
        this.msgs.push({ severity: 'success', summary: '', detail: data.msg.returnCode.code });
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '', detail: data.msg.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '', detail: '' });
    })
  }
  // 
  reset() {
    this.selectedFile = null;
    this.endAfter = null;
    this.state = 'completed';
    this.historyFlowBean.processDefinitionId = '';
  }
  // 
  history(car) {
    this.headerTitle = '';
    this.display = true;
    this.showModel = 2;
    this.historyValue = car;
  }
  historyCall($event) {
    this.display = false;
  }

  //
  paginate(event) {
    //
    this.historyFlowBean.rows = event.rows * 1;
    //
    let currentPage = event.page + 1;
    this.historyFlowBean.page = currentPage;
    //
    this.queryClick();
    this.historyFlowBean.page = 1;
  }
  //
  //
  clickEvn() {
    this.showTree = !this.showTree;
  }
  //
  treeCall(param) {
    this.display = false;
    this.historyFlowBean.id = param.id;
  }
  //
  permissionCheck(id) {
    return this.commfunc.permissionCheck(id)
  }
}
