import { Component, OnInit, Inject, forwardRef } from '@angular/core';
import { TreeNode } from 'primeng/primeng';
import { element } from "protractor";
import { WorkflowHttpService } from '../../../workflow/http/workflow.http.service';
import { Message } from 'primeng/primeng';
import { ConfirmationService } from 'primeng/primeng';
import { FlowCatalogBean } from './bean/flow-catalog.bean'
import { CALENDAR_CN, tableMessage } from 'app/@uisftech/common/constant/config';
import { Commfunc } from "app/@uisftech/common/service/commonFun";

@Component({
  selector: 'flow-catalog',
  templateUrl: './flow-catalog.component.html',
  styleUrls: ['./flow-catalog.component.scss'],
  providers: [ConfirmationService, Commfunc]
})
export class FlowCatalogComponent implements OnInit {
  // 
  precredit: string = '';
  files: TreeNode[];
  selectedFiles: TreeNode[];
  treeList2: string;
  treeSelect2: any;
  //
  msgs: Message[] = [];
  //
  fileTree: any;
  left_tree: any;
  // files: TreeNode[];
  selectedFile: TreeNode;
  // 
  tableData: any;
  flowCatalogBean: FlowCatalogBean = new FlowCatalogBean()
  //
  headerTitle: string = "";
  showModel: any;
  //
  display: boolean = false;
  // 
  updateAddFileValue: any = [];
  //
  updateAddValue: any = [];
  //
  updateFileValue: any = [];
  //
  updateDeleteValue: any = [];
  // 
  //
  updateValue: any = [];
  updateTable: any = [];
  //test
  testSeveice: any = [];
  //
  inValue: string;
  filesTree2: any;
  text: any;
  rows: number = 10;//
  page: number = 1;//
  //
  queryMaintainValue: any;
  //
  visionValue: any;
  tabMesg: any = tableMessage;
  itemB: any;//
  //-1
  act: any = -1;
  fileUrl: string;//
  uniqueReqData: string;//
  handleO: string;//


  waitSelUserList:any;
  invalue:any;
  aaaa:any;
  expandRecursive(node: TreeNode, isExpand: boolean) {
    node.expanded = isExpand;
    if (node.children) {
      node.children.forEach(childNode => {
        this.expandRecursive(childNode, isExpand);
      });
    }
  }
  constructor(
    public workflowHttpService: WorkflowHttpService,
    public confirmationService: ConfirmationService,
    public commfunc: Commfunc
  ) { }

  ngOnInit() {
    this.flowCatalogBean.rows = 10;
    this.flowCatalogBean.page = 1
    this.queryClick()

  }
  //
  expandAll() {

  }
  //
  nodeSelect(event) {
    this.selectedFile = event.node;
    this.updateDeleteValue = event.node
    // 
    let params = {
      "uniqueReqData": event.node.id,
      rows: this.flowCatalogBean.rows,
      page: this.flowCatalogBean.page
    }
    this.fileUrl = '';
    this.workflowHttpService.IProcessDefinitionServiceSearch(params).subscribe((data) => {
      if (data.msg.returnCode.code == 'success') {
        if (data.msg.listArray) {
          this.tableData = [];
          data.msg.listArray.forEach((v1) => {
            this.tableData.push(v1)
          });
          if (this.tableData) {
            this.tableData.forEach(item => {
              this.fileUrl = item.fileUrl;
            })
          }
          this.msgs = [];
          this.msgs.push({ severity: 'success', summary: '', detail: data.msg.returnCode.code })
        }

      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '', detail: data.msg.returnCode.code })
      }
    },
      error => {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '', detail: '' })
      })
  }

  nodeUnselect(event) {
    this.msgs = [];
    this.msgs.push({ severity: 'info', summary: 'Node Unselected', detail: event.node.label });
  }
  toTree(data) {
    data.forEach(item => {
      item['label'] = item['text']
      item['value'] = item
      if (item.children) {
        this.toTree(item.children)
      }
    });
    return data
  }
  //
  queryClick() {
    let param = {
      uniqueReqData: '-1'
    }
    this.workflowHttpService.IProcessDirectoryServiceSearch(param).subscribe(data => {
      if (data.msg.returnCode.code == 'success') {
        this.left_tree = this.toTree(data.msg.listArray);
        this.left_tree.forEach(node => {
          this.expandRecursive(node, true);
        });
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '', detail: data.errorMessage });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '', detail: '' });
    })
  }


  //
  query() {
    this.workflowHttpService.IProcessDefinitionServiceSearch({ "uniqueReqData": this.updateDeleteValue.id }).subscribe((data) => {
      if (data.msg.returnCode.code == 'success') {
        if (data.msg.listArray) {
          this.tableData = [];
          data.msg.listArray.forEach((v1) => {
            this.tableData.push(v1)
          });
        }
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '', detail: data.msg.returnCode.code })
      }
    },
      error => {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '', detail: '' })
      })
  }
  // 
  //  
  addFile() {
    this.headerTitle = '';
    this.display = true;
    this.updateAddFileValue = this.selectedFile;
    this.showModel = 1;
  }
  //
  addFileCall(param) {
    this.display = param;
    this.queryClick();
  }

  // 
  updateFile(item) {
    this.headerTitle = '';
    this.display = true;
    this.updateFileValue = this.selectedFile;
    this.showModel = 2;
  }
  //
  updateFileCall(param) {
    this.display = param;
    let paramId = {
      uniqueReqData: '-1'
    }
    this.workflowHttpService.IProcessDirectoryServiceSearch(paramId).subscribe(data => {
      if (data.msg.returnCode.code == 'success') {
        this.left_tree = this.toTree(data.msg.listArray);
        this.left_tree.forEach(node => {
          this.expandRecursive(node, true);
          this.msgs = [];
          this.msgs.push({ severity: 'success', summary: '', detail: '' })
        });
      }
    })
  }



  // 
  deleteFile(item) {
    this.confirmationService.confirm({
      message: '?',
      accept: () => {
        //
        let param = { "uniqueReqData": this.updateDeleteValue.id };
        this.workflowHttpService.IProcessDirectoryServiceDelete(param).subscribe(data => {
          if (data.msg.returnCode.code == 'success') {
            this.msgs = [];
            this.msgs.push({ severity: 'success', summary: '', detail: '' });
            this.queryClick();
            this.selectedFile={};
          } else {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '', detail: data.msg.returnCode.message });//
          }
        }, error => {
          this.msgs = [];
          this.msgs.push({ severity: 'error', summary: '', detail: "" });
        })
      }
    });
  }

  // 
  into() {
    this.headerTitle = '';
    this.showModel = 11;
    this.display = true;
  }
  intoCall(param) {
    this.display = param;
    this.msgs = [];
    this.msgs.push({ severity: 'success', summary: '', detail: '' })
  }
  // 
  out() {
    this.headerTitle = '';
    this.showModel = 10;
    this.display = true;
  }
  outCall(param) {
    this.display = param;
    this.msgs = [];
    this.msgs.push({ severity: 'success', summary: '', detail: '' })
  }
  /**
   * 
   */
  // 
  add() {
    this.headerTitle = '';
    this.display = true;
    this.updateAddValue = this.selectedFile;
    this.showModel = 4;
  }

  //
  addCall(param) {
    this.display = param;
    this.query();
  }

  // 
  update(item) {
    this.headerTitle = '';
    // this.updateValue = this.selectedFile;
    this.updateTable = item;
    this.updateValue = item;
    this.display = true;
    this.showModel = 5;
  }

  //
  updateCall(param) {
    this.display = param;
    this.flowCatalogBean = new FlowCatalogBean();
    this.workflowHttpService.IProcessDefinitionServiceSearch({ "uniqueReqData": this.updateDeleteValue.id }).subscribe((data) => {
      if (data.msg.returnCode.code == 'success') {
        if (data.msg.listArray) {
          this.tableData = [];
          data.msg.listArray.forEach((v1) => {
            this.tableData.push(v1)
          });
          this.msgs = [];
          this.msgs.push({ severity: 'success', summary: '', detail: '' })
        }

      }
    }
    )
  }


  //
  delete(item) {
    this.confirmationService.confirm({
      message: '?',
      accept: () => {
        //
        let param = { "uniqueReqData": item.id };
        this.workflowHttpService.IProcessDefinitionServiceDeleteProcess(param).subscribe(data => {
          if (data.msg.returnCode) {
            if (data.msg.returnCode.code == 'success') {
              this.msgs = [];
              this.msgs.push({ severity: 'success', summary: '', detail: '' });
              this.query();
            } else {
              this.msgs = [];
              this.msgs.push({ severity: 'error', summary: '', detail: data.msg.returnCode.message });
            }
          }
        }, error => {
          this.msgs = [];
          this.msgs.push({ severity: 'error', summary: '', detail: "" });
        })
      }
    });
  }

  // 
  design(item) {
    window.open('http://' + item.fileUrl + ':8080' + '/wfd/p/editor?id=' + item.code + '&wfversion=' + item.processVersion + '&name=' + item.name)
  }

  //  
  release(item) {
    this.workflowHttpService.IWorkFlowServicedeployProcess({ "uniqueReqData": item.id }).subscribe(data => {
      if (data.msg.returnCode.code == 'success') {
        this.msgs = [];
        this.msgs.push({ severity: 'success', summary: '', detail: '' });
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '', detail: data.msg.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '', detail: "" });
    })
  }

  // 
  maintain(item) {
    this.headerTitle = '';
    this.display = true;
    this.showModel = 6;
    this.queryMaintainValue = item;
  }
  queryMaintainCall(param) {
    this.display = param;
    this.queryClick();
  }

  // 
  visit() {
    this.headerTitle = '';
    this.display = true;
    this.showModel = 7;
  }

  // 
  use() {
    this.headerTitle = '';
    this.display = true;
    this.showModel = 8;
  }

  // 
  lookVersion(item) {
    this.headerTitle = '';
    this.display = true;
    this.showModel = 9;
    this.visionValue = item;
  }
  //
  otherVersion(param) {
    this.display = param;
    this.queryClick();
    this.msgs = [];
    this.msgs.push({ severity: 'success', summary: '', detail: '' });
  }
  //  
  paginate(event) {
    //
    this.flowCatalogBean.rows = event.rows * 1;
    //
    let currentPage = event.page + 1;
    this.flowCatalogBean.page = currentPage;
    //
    this.queryClick();
    this.flowCatalogBean.page = 1;

  }
  //
  permissionCheck(id) {
    return this.commfunc.permissionCheck(id)
  }




  // clickBtb(){
  //   this.waitSelUserList= this.commfunc.query('245000','');
  //   console.log(this.waitSelUserList.erro)
  //   if(this.waitSelUserList.erro){
  //     this.msgs=[]
  //     this.msgs.push({ severity: 'error', summary: '', detail: this.waitSelUserList.erro.message });
  //   }
  //   this.invalue=this.waitSelUserList.waitSelUserList;
  //   this.aaaa=this.waitSelUserList.display;
  // }
}
