import { Component, OnInit } from '@angular/core';
import { PaginatorModule } from 'primeng/primeng';
import { WorkflowHttpService } from '../../../workflow/http/workflow.http.service';
import { Message } from 'primeng/primeng';
import { ConfirmationService } from 'primeng/primeng';
import { FlowDemoBean } from './bean/flowDemo.bean';
import { TreeNode } from 'primeng/primeng';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { CALENDAR_CN, tableMessage } from 'app/@uisftech/common/constant/config';

@Component({
  selector: 'flow-demo',
  templateUrl: './flow-demo.component.html',
  styleUrls: ['./flow-demo.component.scss']
})
export class FlowDemoComponent implements OnInit {
  tabMesg: any = tableMessage;
  aaa: any;
  // 
  precredit: string = '';

  //
  overValue: any = [];
  //
  historyValue: any = [];
  // 
  tableData: any;
  //
  total: number;
  total1: string;//
  //
  headerTitle: string = '';
  showModel: any;
  //
  display: boolean = false;
  //
  inValue: string;
  //
  fileTree: any;
  left_tree: any;
  //
  msgs: Message[] = [];
  //  
  selectedFile: TreeNode;
  //
  addDisplay: boolean = false;  //
  id: string;//id
  flowDemoBean: FlowDemoBean = new FlowDemoBean();
  treeData: any;
  desc: string;//
  startTime: any;//
  endAfter: any;
  ch: any = CALENDAR_CN;        //
  temData: Date = new Date();   //

  showTree: Boolean = false;
  //value
  choiceValue: any;
  // 
  expandRecursive(node: TreeNode, isExpand: boolean) {
    node.expanded = isExpand;
    if (node.children) {
      node.children.forEach(childNode => {
        this.expandRecursive(childNode, isExpand);
      });
    }
  }
  processDefinitionId: string;//
  constructor(
    public workflowHttpService: WorkflowHttpService,
    public commfunc: Commfunc
  ) { }

  ngOnInit() {
    this.flowDemoBean.rows = 10;
    this.flowDemoBean.page = 1;
    this.queryClick1();
    this.queryClick();
    //
    let _thiss = this;
    document.onkeydown = function (event) {
      if (event && event.keyCode === 13) {
        _thiss.queryClick();
      }
    }
  }

  //
  toTree(data) {
    data.forEach(item => {
      item['label'] = item['text']
      item['value'] = item
      if (item.children) {
        this.toTree(item.children)
      }
    });
    return data
  }
  //
  nodeSelect(event) {
    if (event.node) {
      this.choiceValue = event.node.value.id;
      this.selectedFile = event.node.label;
      this.showTree = false
    }
  }

  nodeUnselect(event) {
    this.msgs = [];
    this.msgs.push({ severity: 'info', summary: 'Node Unselected', detail: event.node.label });
  }
  //
  queryClick1() {
    this.workflowHttpService.IProcessDirectoryServiceSearchForDesigner({ 'uniqueReqData': '-1' }).subscribe(data => {
      if (data.msg.returnCode.code == 'success') {
        this.left_tree = this.toTree(data.msg.listArray);
        this.left_tree.forEach(node => {
          this.expandRecursive(node, true);
        });
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '', detail: data.errorMessage });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '', detail: '' });
    })
  }
  //
  queryClick() {
    this.total1 = '';
    if (this.selectedFile) {
      this.flowDemoBean.processDefinitionId = this.choiceValue;
    }
    this.flowDemoBean.endAfter = this.commfunc.transDateN(this.endAfter);
    let ch = JSON.parse(this.commfunc.getSessionData('commonHeader'));
    this.flowDemoBean.userId = ch['userId'];
    this.flowDemoBean.state = 'active'
    this.workflowHttpService.IWorkFlowServicegetProcessInsList(this.flowDemoBean).subscribe(data => {
      if (data.msg.returnCode.code == 'success') {
        this.tableData = data.msg.data
        this.total = data.msg.total;
        if (this.total == 0) {
          this.total1 = '0'
        }
        this.msgs = [];
        this.msgs.push({ severity: 'success', summary: '', detail: data.msg.returnCode.code });
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '', detail: data.msg.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '', detail: '' });
    })
  }
  //
  //
  clickEvn() {
    this.showTree = !this.showTree;
  }
  //
  treeCall(param) {
    this.display = false;
    this.flowDemoBean.id = param.id;
  }
  // 
  over(car) {
    this.headerTitle = '';
    this.display = true;
    this.showModel = 1;
    this.overValue = car;
  }
  //
  overCall(param) {
    this.display = false;
    this.workflowHttpService.IWorkFlowServicegetProcessInsList(this.flowDemoBean).subscribe(data => {
      if (data.msg.returnCode.code == 'success') {
        this.tableData = data.msg.data
        this.total = data.msg.total;
        this.msgs = [];
        this.msgs.push({ severity: 'success', summary: '', detail: '' });
      }

    })
  }
  // 
  history(car) {
    this.headerTitle = '';
    this.display = true;
    this.showModel = 2;
    this.historyValue = car;
  }
  //
  historyCall(param) {
    this.display = false;
    // this.queryClick();
  }
  // 
  reset() {
    this.selectedFile = null;
    this.endAfter = null;
    this.flowDemoBean.processDefinitionId = '';
    this.flowDemoBean.processInstanceId = '';
  }
  //
  paginate(event) {
    //
    this.flowDemoBean.rows = event.rows * 1;
    //
    let currentPage = event.page + 1;
    this.flowDemoBean.page = currentPage;
    //
    this.queryClick();
    this.flowDemoBean.page = 1;
  }
  //
  showData() {
    this.temData = this.endAfter;
  }
  //
  permissionCheck(id) {
    return this.commfunc.permissionCheck(id)
  }
}
