
import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { MaintainBean } from './bean/maintain.bean'
import { WorkflowHttpService } from '../../../../workflow/http/workflow.http.service';
import { SharedBasisModule } from 'app/sharedBasis.module';
import { ConfirmationService } from 'primeng/primeng';
import { Message } from 'primeng/primeng';
import { CALENDAR_CN, tableMessage } from 'app/@uisftech/common/constant/config';

@Component({
    selector: 'maintain',
    templateUrl: './maintain.html',
    styleUrls:['maintain.scss'],
    providers: [WorkflowHttpService, ConfirmationService]
})
export class Maintain {

    @Input() inValue: any;

    @Output() outValue: EventEmitter<any> = new EventEmitter();
    //
    precredit: string;
    // 
    tableData: any;
    //bean
    maintainBean: MaintainBean = new MaintainBean()
    //
    total: string;
    //
    headerTitle: string;
    //
    display: boolean = false;

    showModel: string;

    //
    updateValue: any = [];

    //
    msgs: Message[] = [];

    //list
    maintainB: any;
    //
    addValue: any;
    //
    tabMesg: any = tableMessage;
    constructor(
        public workflowHttpService: WorkflowHttpService,
        public confirmationService: ConfirmationService
    ) {

    }
    ngOnChanges() {
        this.fuzhi();
    }

    fuzhi() {
        this.maintainB = this.inValue;
    }

    ngOnInit() {
        this.query();
    }

    //
    query() {
        let param = { uniqueReqData: this.maintainB.code }
        this.workflowHttpService.IDictConfServicegetProcessDicts(param).subscribe(data => {
            if (data.msg.returnCode) {
                if (data.msg.returnCode.code == 'success') {
                    this.tableData = data.msg.listArray;
                    this.total = data.total;
                } else {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '', detail: data.msg.returnCode.code });
                }
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '', detail: '' });
        })
    }

    // 
    add() {
        this.headerTitle = '';
        this.display = true;
        this.showModel = '1';
        this.addValue = this.maintainB
    }
    //
    addCall(param) {
        this.display = param;
        let param1 = { uniqueReqData: this.maintainB.code };
        this.workflowHttpService.IDictConfServicegetProcessDicts(param1).subscribe(data => {
            if (data.msg.returnCode) {
                if (data.msg.returnCode.code == 'success') {
                    this.tableData = data.msg.listArray;
                    this.total = data.total;
                    this.msgs = [];
                    this.msgs.push({ severity: 'success', summary: '', detail: '' });
                }
            }
        })
    }

    //
    update(car) {
        this.headerTitle = '';
        this.display = true;
        this.showModel = '2';
        this.updateValue = car;
    }
    //
    updateCall(param) {
        this.display = param;
        this.maintainBean = new MaintainBean();
        let param1 = { uniqueReqData: this.maintainB.code };
        this.workflowHttpService.IDictConfServicegetProcessDicts(param1).subscribe(data => {
            if (data.msg.returnCode.code == 'success') {
                this.tableData = data.msg.listArray;
                this.total = data.total;
                this.msgs = [];
                this.msgs.push({ severity: 'success', summary: '', detail: '' });
            }

        })
    }

    //
    waitQuery() {

    }
    //
    delete(car) {
        this.confirmationService.confirm({
            message: '',
            accept: () => {
                this.workflowHttpService.IDictConfServiceDeleteProcessVariable({ "uniqueReqData": car.id }).subscribe(data => {
                    if (data.msg.returnCode.code == "success") {
                        this.msgs = [];
                        this.msgs.push({ severity: 'success', summary: '', detail: '' });
                        this.query();
                    } else {
                        this.msgs = [];
                        this.msgs.push({ severity: 'error', summary: '', detail: data.msg.returnCode.message });
                    }
                }, error => {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '', detail: "" });
                })
            },
            reject: () => {
            }
        });
    }
}